import { Dayjs } from "dayjs";
import { PanelMode } from "../date-util";

type IBodyProps = {
  value: Dayjs | null;
  viewDate: Dayjs;
  onSelect: (value: Dayjs) => void;
  disabledDate?: (date: Dayjs) => boolean;
}

export type IMonthBodyProps = IBodyProps;
export type IDateBodyProps = IBodyProps;

type IPanelProps = IBodyProps & {
  showNow?: boolean;
  nowNode?: React.ReactNode;
  onPanelChange: (origin: PanelMode, target: PanelMode) => void;
}

export type IYearPanelProps = IPanelProps;
export type IMonthPanelProps = IPanelProps;

export type IDatePanelProps = Omit<IPanelProps, 'viewDate'> & {
  showTime?: boolean;
  curPanel: PanelMode;
};

export type IDateTimePanelProps = IDatePanelProps & {
  timeFormat?: 'hm' | 'hms';
};

export type ITimePanelProps = Pick<IDateTimePanelProps, 'value' | 'onSelect' | 'showNow' | 'timeFormat'> & {
  showHeader?: boolean;
}